.Backdrop{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 100;
    left: 0;
    top: 0;
    background-color: rgba(0,0,0,0.7);
    text-align: center;
    color: #eee;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.Backdrop{
    -webkit-animation: appear 200ms ease 100ms;
    animation: appear 200ms ease 100ms;
    -webkit-animation-fill-mode: backwards;
    animation-fill-mode: backwards;
    -webkit-transition-property: none;
    -o-transition-property: none;
    transition-property: none; }
    @-webkit-keyframes appear {
            0% {
              opacity: 0;
              -webkit-transform: scale(0);
              transform: scale(0); }
          
            100% {
              opacity: 1;
              -webkit-transform: scale(1);
              transform: scale(1); } }
          
    @keyframes appear {
            0% {
              opacity: 0;
              -webkit-transform: scale(0);
              transform: scale(0); }
          
            100% {
              opacity: 1;
              -webkit-transform: scale(1);
              transform: scale(1); } }
.Backdrop h1{
    font-size: 50px;
    line-height: 0;
}
.Backdrop h3{
    font-size: 30px;
}
.NewGame {
    background:-webkit-linear-gradient(315deg, #68cce8 0%,#2ea6c1 100%);
    background:-o-linear-gradient(315deg, #68cce8 0%,#2ea6c1 100%);
    background:linear-gradient(135deg, #68cce8 0%,#2ea6c1 100%);
    cursor: pointer;
    border-radius: 5px;
    width: 200px;
    height: 50px;
    margin: 0 auto;
    padding: 10px;
    color: #111;
    font-weight: bold;
    font-size: 30px;
    line-height: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    
  }
  .NewGame:hover {
    background: #ccc;
  }

@media screen and (max-width: 481px) {
    .Backdrop h1{
      font-size: 30px;
      line-height: 0;
  }
    .Backdrop h3{
        font-size: 20px;
    }
    .NewGame {
      width: 130px;
      font-size: 20px;
      height: 30px;
    }
}


