.GameContainer {
  display: block;
  margin: auto;
  padding: 15px;
  cursor: default;
  touch-action: none;
  background: #222;
  border-radius: 6px;
  width:500px;
  height: 500px;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -ms-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -ms-touch-action: none;
  touch-action: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; 
  position: relative;
  margin-top: 40px;
}


@media screen and (max-width: 521px) {
      .GameContainer {
          margin-top: 40px;
          padding: 12px;
          width: 450px;
          height: 450px;}
    }
    
@media screen and (max-width: 481px) {
  .GameContainer {
    margin-top: 17px;
    padding: 10px;
    width: 280px;
    height: 280px; }
}



